.real-utils-cross-pointer {
    z-index: 999999 !important;

    img {
        display: block;
        max-width: 100%;
        margin-bottom: 8px;
    }

    label {
        zoom: 0.9;
        display: block;
        opacity: 0.8;
        margin-top: 5px;
    }

    h3:before {
        content: "\f487";
        top: 50%;
        margin-top: -16px;
    }
}

.real-utils-pointer-buttons {
    .button {
        margin-left: 5px;
    }
}
